.popular-section {
  padding: 20px 0 20px 0;
}

.popular-container {
  width: 100%;
}

.popular-title {
  color: var(--light-theme-text-color);
  font-size: 18px;
  font-weight: 600;
  line-height: 1.33;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.popular-err {
  color: var(--light-theme-almost-transparent-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

.popular-recipe {
  display: flex;
  gap: 16px;
}

.popular-recipe:not(:last-child) {
  margin-bottom: 24px;
}

.popular-img {
  width: 64px;
  height: 64px;
  min-width: 64px;
  border-radius: 10px;
  overflow: hidden;
  object-fit: cover;
}

.popular-desc-container {
  max-width: 252px;
}

.popular-recipe-title {
  color: var(--light-theme-text-color);
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28;
  text-transform: uppercase;
  margin-bottom: 8px;
}

.popular-recipe-descr {
  color: var(--light-theme-text-color);
  font-size: 12px;
  line-height: 1.33;
}

/* MEDIA MOBILE */
@media screen and (min-width: 375px) {
  .popular-container {
    width: 335px;
  }
}

/* MEDIA TABLET */
@media screen and (min-width: 768px) {
  .popular-section {
    padding: 32px 0 0 0;
  }

  .popular-container {
    width: 176px;
  }

  .popular-recipe {
    gap: 8px;
  }

  .popular-recipe:not(:last-child) {
    margin-bottom: 16px;
  }

  .popular-img {
    width: 48px;
    height: 48px;
    min-width: 48px;
  }

  .popular-title {
    margin-bottom: 32px;
  }

  .popular-desc-container {
    max-width: 120px;
  }

  .popular-recipe-descr {
    font-size: 10px;
    line-height: 1.4;
  }
}

/* MEDIA DESKTOP */
@media screen and (min-width: 1200px) {
  .popular-container {
    width: 235px;
  }

  .popular-recipe {
    gap: 16px;
  }

  .popular-recipe:not(:last-child) {
    margin-bottom: 32px;
  }

  .popular-img {
    width: 64px;
    height: 64px;
    min-width: 64px;
  }

  .popular-desc-container {
    max-width: 155px;
  }

  .popular-title {
    font-size: 24px;
    line-height: 1.16;
  }
}
